.gantt .grid-background {
  fill: none; }

.gantt .grid-header {
  fill: #eee !important;
  stroke: #e0e0e0;
  stroke-width: 1.4; }

.gantt .grid-row {
  fill: #ffffff; }

.gantt .grid-row:nth-child(even) {
  fill: #f5f5f5; }

.gantt .row-line {
  stroke: #ebeff2; }

.gantt .tick {
  stroke: #000 !important;
  stroke-width: 0.2; }
.gantt .tick.thick {
  stroke-width: 0.4; }

.gantt .today-highlight {
  fill: #000;
  opacity: 0.5; }

.gantt .arrow {
  fill: none;
  stroke: #666;
  stroke-width: 1.4; }

.gantt .bar {

  stroke: #8D99A6;
  stroke-width: 0;
  transition: stroke-width .3s ease;
  user-select: none; }

.gantt .bar-progress {
  fill: #1b75bb !important; }

.gantt .bar-invalid {
  fill: transparent;
  stroke: #8D99A6;
  stroke-width: 1;
  stroke-dasharray: 5; }
.gantt .bar-invalid ~ .bar-label {
  fill: #555; }

.gantt .bar-label {
  fill: #fff;
  dominant-baseline: central;
  text-anchor: middle;
  font-size: 12px;
  font-weight: lighter; }
.gantt .bar-label.big {
  fill: #555;
  text-anchor: start; }

.gantt .handle {
  fill: #ddd;
  cursor: ew-resize;
  opacity: 0;
  visibility: hidden;
  transition: opacity .3s ease; }

.gantt .bar-wrapper {
  cursor: pointer; }
.gantt .bar-wrapper:hover .bar {
   }
.gantt .bar-wrapper:hover .bar-progress {
  fill: #8a8aff; }
.gantt .bar-wrapper:hover .handle {
  visibility: visible;
  opacity: 1; }
.gantt .bar-wrapper.active .bar {
  fill: #a9b5c1; }
.gantt .bar-wrapper.active .bar-progress {
  fill: #8a8aff; }

.gantt .lower-text, .gantt .upper-text {
  font-size: 12px;
  text-anchor: middle; }

.gantt .upper-text {
  fill: #555; }

.gantt .lower-text {
  fill: #333; }

.gantt .hide {
  display: none; }

.gantt-container {
  position: relative;
  font-size: 12px; }
.gantt-container .popup-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.8);
  padding: 0;
  color: #959da5;
  border-radius: 3px; }
.gantt-container .popup-wrapper .title {
  border-bottom: 3px solid #a3a3ff;
  padding: 10px; }
.gantt-container .popup-wrapper .subtitle {
  padding: 10px;
  color: #dfe2e5; }
.gantt-container .popup-wrapper .pointer {
  position: absolute;
  height: 5px;
  margin: 0 0 0 -5px;
  border: 5px solid transparent;
  border-top-color: rgba(0, 0, 0, 0.8); }